button {
    padding: 0.65rem 1.55rem;
    font-size: .8em;
    border: 2px solid #000;
    color: #000;
    margin-block: 1rem;
    background: transparent;
    font-weight: 600;
    cursor: pointer;
    transition: .5s;
    &:hover {
        background-color: #000;
        color: #fff
    }
}